.contacts {
  padding-top: 40px;
  padding-bottom: 40px;
}

.contacts__container {
  margin-top: 20px;
  display: flex;
  @media screen and (min-width: 280px) {
    flex-direction: column;
    flex-direction: column-reverse;
    justify-content: center;
  }

  @media screen and (min-width: $tablet) {
    display: flex;
    flex-direction: row;
  }
}

.contacts__map {
  @media screen and (min-width: $mobile_280) {
    width: 100%;
  }

  @media screen and (min-width: $tablet) {
    width: calc(100% / 2);
  }
}

.contacts__map_img {
  display: inline-block;
  // width: 100%;
}

.contacts__adress {
  @media screen and (min-width: $mobile_280) {
    padding-bottom: 20px;
    width: 100%;
  }

  @media screen and (min-width: $tablet) {
    padding-bottom: 0;
    width: calc(100% / 2);
  }
}

.contacts__list {
  display: flex;
  flex-direction: column;
  text-align: left;

  @media screen and (min-width: $tablet) {
    margin-left: 50px;
  }
}

.contacts__item {
  @media screen and (min-width: $mobile_280) {
    text-align: center;
  }

  @media screen and (min-width: $tablet) {
    text-align: left;
  }
  margin-top: 30px;
}

.contacts__item_title {
  font-weight: 500;
  font-size: 20px;
  line-height: 1.3;

  &::before {
    content: "";
  }
}

.contacts__item_description {
  margin-top: 10px;
  color: $buttonColor;
}

.contacts__messages_list {
  display: flex;

  @media screen and (min-width: $mobile_280) {
    justify-content: center;
  }

  @media screen and (min-width: $tablet) {
    justify-content: left;
  }
}

.contacts__messages_item {
  &:not(:first-child) {
    margin-left: 10px;
  }
}

.contacts__messages_icon {
  width: 25px;
  height: 25px;
  fill: $textColor;

  &:hover,
  &:active {
    fill: $buttonColor;
  }
}
